.about-text h1{margin:0 0 30px 4px;color:var(--lightest-gray);font-family:var(--font-family);font-size:clamp(26px,5vw,var(--fs-heading))}.about-text h1::after{content:"";display:inline-block;height:.5em;vertical-align:bottom;width:70%;margin-right:-100%;margin-left:20px;border-top:1px solid var(--gray)}.about-text p{font-family:var(--font-family);font-size:var(--fs-md);margin:20px 0 0;line-height:2.3rem}.animateHover a{text-decoration:none;color:var(--blue);font-weight:bold;padding-bottom:2px;background-image:linear-gradient(var(--blue) 0 0);background-position:0 100%;background-size:0% 1px;background-repeat:no-repeat;transition:background-size .3s,background-position 0s .5s}.animateHover a:hover{background-position:100% 100%;background-size:100% 1px}.skills-list ul{max-width:500px;display:flex;flex-wrap:wrap;list-style:none;margin:0;padding:0}.skills-list li{flex:1 1 40%;padding:10px;font-family:var(--font-mono);font-size:var(--fs-xs)}.skills-list li::before{content:"âž£";color:var(--blue);padding-right:12px;font-size:var(--fs-sm)}